<template>
    <div>
        <form v-on:submit.prevent>
			검색:<select v-model="condition">
                    <option disabled value="">Please select one</option>
					<option value="title">제&nbsp;&nbsp;&nbsp;&nbsp;목</option>			
					<option value="name">글쓴이</option>			
				</select>&nbsp;
			<input type="text" v-model.lazy="word" @keyup.enter="searchQnA"/>
			<input type="button" @click="searchQnA" value="검색" />
		</form>
        <br />
        <div class="container">
            <table border="1">
                <th>번호</th>
                <th>제목</th>
                <th>아이디</th>
                <th>작성일</th>

                <tr v-for="qna in qnas" :key="qna.num">
                    <td>{{qna.num}}</td>
                    <td>
                        <router-link :to="`/detail/${qna.num}`">{{qna.title}}</router-link>
                    </td>
                    <td>{{qna.id}}</td>
                    <td>{{qna.regtime}}</td>
                </tr>
            </table>
	    </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data(){
        return {
            condition: '',
            word: "",
        };
    },
    created(){
        this.$store.state.qnas = [];
    },

    computed: {
        ...mapState(["qnas"]),
    },

    methods: {
        searchQnA(){
            if(this.word == ""){
                this.$store.dispatch("ALLQNA");
            }else{
                this.$store.dispatch("SEARCHQNA", {
                    condition: this.condition,
                    word: this.word,
                });
            }
        }
    },
}
</script>

